.service-card {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 16px;
  transition: transform 0.2s;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Vollkorn', serif;
  font-weight: 400;
  cursor: pointer;
}

.service-card:hover {
  transform: scale(1.02);
}

.service-card h3 {
  font-size: 20px;
  margin-bottom: 8px;
}

.service-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
  border-radius: 8px;
}

.service-details p {
  margin: 4px 0;
  font-size: 14px;
  color: #666;
}

.dark-mode .service-details p {
  color: #ccc;
}
